.header {
  margin-bottom: 100px;

  background-size: cover;
  background-position: center;
  background-image: url(../images/image-3.svg);

  @include onTablet {
    margin-bottom: 140px;
  }

  @include onDesktop {
    margin-bottom: 140px;
  }

  &__content {
    display: flex;
    flex-direction: column;

    height: 100vh;
    padding: $headerTopPadding 0;
    box-sizing: border-box;

    @include onTablet {
      padding: 32px 0;
    }

    @include onDesktop {
      padding: 32px 0;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: #fff;
    text-align: center;
    padding-top: 129px;

    @include onTablet {
      font-size: 64px;
      line-height: 64px;
      padding-top: 159px;
    }

    @include onDesktop {
      font-size: 80px;
      line-height: 80px;
      padding-top: 103px;
    }
  }

  &__call {
    text-decoration: none;
    @include onDesktop {
      &::before {
        content: "The Met Fifth Avenue\a+1 212-535-7710";
        white-space: pre;
        position: relative;
        top: 50%;
        transform: translate(-100%, -70%);

        display: block;
        width: 210px;
        padding-right: 20px;

        font-weight: 700;
        font-size: 16px;
        line-height: 27px;
        text-transform: uppercase;
        text-align: end;
        color: #fff;
        opacity: 0;
        pointer-events: none;

        transition: 0.5s;
      }

      &::after {
        content: "\aThe Met Cloisters\a +1 212-923-3700";
        white-space: pre;
        position: relative;
        top: 50%;
        transform: translate(-100%, -70%);

        display: block;
        width: 210px;
        padding-right: 20px;

        font-weight: 700;
        font-size: 16px;
        line-height: 27px;
        text-transform: uppercase;
        text-align: end;
        color: #fff;
        opacity: 0;
        pointer-events: none;

        transition: 0.5s;
      }
    }

    &:hover::before {
      opacity: 1;
      pointer-events: all;
    }

    &:hover::after {
      opacity: 1;
      pointer-events: all;
    }
  }
}
